import React, { useEffect, useState, useMemo } from 'react';
import logo from './logo.svg';
import './App.css';
import { GET } from './utils/requests';

interface TournamentProps {
    match: {
        params: {
            id: string
        }
    }
}

interface Player {
	id: string
	tournamentId: string
	firstName: string 
	lastName: string
	rating: number

	performanceRating: number
	expectedScore: number
	expectedGameScore: number
	whiteTotal: number
	score: number
	gamePoints: number
	played: number

	active: boolean
	isPlaying: string
	histories: History[]

	createdAt: string
	updatedAt: string
}

type GameScore = 0 | 0.5 | 1;

interface History {
    id: string
	oppRating: number
	expected: number
    colour: 'W' | 'B' | 'N'
	game: number | undefined
    match: GameScore | undefined
	played: number
}

interface TournamentStatus {
	round: number
	name: string
	id: string
	active?: boolean
}

export default function Tournament(props: TournamentProps) {

    const id = props.match.params.id
    const [loaded, setLoaded] = useState(false);
    const [state, setState] = useState({});
    const [settings, setSettings] = useState({});
    const [status, setStatus] = useState({} as TournamentStatus);
    const [players, setPlayers] = useState([] as Player[]);
    const [pairings, setPairings] = useState([] as [string, string][]);

    const playerDict: Map<string, Player> = useMemo(() => {
        if (!players.length) return new Map();
        let dict = new Map();
        for (let p of players) dict.set(p.id, p);
        return dict;
    }, [players]);

    useEffect(() => {
        Promise.all([
            GET({ url: '/tournament/' + id + '/fetchPlayers'})
                .then(setPlayers),
            GET({ url: '/tournament/' + id + '/getPairings'})
                .then(setPairings),
            GET({ url: '/tournament/' + id + '/getStatus'})
                .then(setStatus)
        ]).then(() => setLoaded(true)) /*
        GET({ url: '/tournament/' + id + '/getSettings'})
            .then(setSettings);
        GET({ url: '/tournament/' + id + '/getState'})
            .then(setState);*/

    }, [setPairings, setPlayers]);

    const round = status.round || 0;

    useEffect(() => {
        if (!round) return;
        let histories = new Array(round + 1);
        for (let i = 1; i < round + 1; i++) histories[i] = {
            match: 0,
            game: 0
        };
        playerDict.set('bye', {
            id: 'bye',
            firstName: 'BYE',
            histories
        } as any as Player);
    }, [playerDict, round]);

    if (!loaded) return null;

    return (

        <div id='main'>
            <div className='container'>
                    <h1>{status.name}</h1>

                    <section>
                        <h2>Pairings</h2>
                        <table className='download-table full-width'>
                            <thead>
                                <tr>
                                    <td style={{width: '45%'}}>White</td>
                                    <td style={{width: '3%'}}></td>
                                    <td style={{width: '3%'}}></td>
                                    <td style={{width: '3%'}}></td>
                                    <td style={{width: '45%'}}>Black</td>
                                </tr>
                            </thead>
                            <tbody>
                                {pairings.map((row) => {
                                    let w = playerDict.get(row[0].trim());
                                    let b = playerDict.get(row[1].trim());
                                    return (
                                        <tr>
                                            <td>{w?.firstName}</td>
                                            <td className={'bold'}>{w?.histories[round]?.game}</td>
                                            <td>-</td>
                                            <td className={'bold'}>{b?.histories[round]?.game}</td>
                                            <td>{b?.firstName}</td>
                                        </tr>
                                    )
                                })}
                            </tbody>
                        </table>
                    </section>


            </div>
        </div>
    );
}
